    src: url('./assets/ChalkboardSE-Regular.ttf')
}

body {
  margin: 0;
  background: #eee;
}

#app {
  font-family: chalk, "Avenir", Helvetica, Arial, sans-serif;
  color: #fff;
  background: url('./assets/bg.png') repeat;
  background-size: 450px 466.5px;
}

.component-header {
    align-items: center;
    box-sizing: border-box;
    display: flex;
    position: relative;
}

.btn-box {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    padding: 5px;
    width: 100%;
    max-width: 1200px;
    text-align: center
}

.help-btn, .tele-btn, .lang-btn {
    display: inline-block;
    margin-left: 5px;
    color: #fff !important;
    width: 70px;
    height: 24px;
    background: #3399ff;
    font-size: 14px;
    line-height: 22px;
}

.tele-btn {
    background: #339966;
}

.lang-btn {
    background: #ff8b00;
}

.lang-box {
    position: absolute;
    top: 100%;
    right: 5px;
}

.lang-box li {
    background: #efefef;
    color: #000;
    width: 70px;
    height: 24px;
    cursor: pointer;
}

.lang-box li:hover {
    background: #b5b5b6;
}

.component-header .center {
  align-items: center;
  color: #fff;
  display: flex;
  font-size: 16px;
  font-weight: 700;
  height: 54px;
  width: 100%;
  justify-content: center;
  line-height: 1;
}

.component-header .full {
  width: 100%;
  background: url('./assets/title-bg.png') repeat;
  text-align: center;
}

.full img {
  display: block;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.full img.phone {
  display: none;
}

.help {
  position: fixed;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 860px;
  padding: 20px;
  padding: 1.25rem;
  z-index: 3;
  font-size: 18px;
  box-sizing: border-box;
}

.help-container {
  background: #fff;
  color: #000;
  border: 2px solid #000;
  padding: 20px;
  padding: 1.25rem;
  max-height: 600px;
  overflow: auto;
}

.help h5 {
  color: #009944;
}

.help h4 {
  text-align: center;
}

.tooltip {
  display: block !important;
  z-index: 10000;
}

.tooltip .tooltip-inner {
  background: black;
  color: white;
  border-radius: 16px;
  padding: 5px 10px 4px;
}

.tooltip .tooltip-arrow {
  width: 0;
  height: 0;
  border-style: solid;
  position: absolute;
  margin: 5px;
  border-color: black;
  z-index: 1;
}

.tooltip[x-placement^='top'] {
  margin-bottom: 5px;
}

.tooltip[x-placement^='top'] .tooltip-arrow {
  border-width: 5px 5px 0 5px;
  border-left-color: transparent !important;
  border-right-color: transparent !important;
  border-bottom-color: transparent !important;
  bottom: -5px;
  left: calc(50% - 5px);
  margin-top: 0;
  margin-bottom: 0;
}

.tooltip[x-placement^='bottom'] {
  margin-top: 5px;
}

.tooltip[x-placement^='bottom'] .tooltip-arrow {
  border-width: 0 5px 5px 5px;
  border-left-color: transparent !important;
  border-right-color: transparent !important;
  border-top-color: transparent !important;
  top: -5px;
  left: calc(50% - 5px);
  margin-top: 0;
  margin-bottom: 0;
}

.tooltip[x-placement^='right'] {
  margin-left: 5px;
}

.tooltip[x-placement^='right'] .tooltip-arrow {
  border-width: 5px 5px 5px 0;
  border-left-color: transparent !important;
  border-top-color: transparent !important;
  border-bottom-color: transparent !important;
  left: -5px;
  top: calc(50% - 5px);
  margin-left: 0;
  margin-right: 0;
}

.tooltip[x-placement^='left'] {
  margin-right: 5px;
}

.tooltip[x-placement^='left'] .tooltip-arrow {
  border-width: 5px 0 5px 5px;
  border-top-color: transparent !important;
  border-right-color: transparent !important;
  border-bottom-color: transparent !important;
  right: -5px;
  top: calc(50% - 5px);
  margin-left: 0;
  margin-right: 0;
}

.tooltip.popover .popover-inner {
  background: #f9f9f9;
  color: black;
  padding: 24px;
  border-radius: 5px;
  box-shadow: 0 5px 30px rgba(black, .1);
}

.tooltip.popover .popover-arrow {
  border-color: #f9f9f9;
}

.tooltip[aria-hidden='true'] {
  visibility: hidden;
  opacity: 0;
  transition: opacity .15s, visibility .15s;
}

.tooltip[aria-hidden='false'] {
  visibility: visible;
  opacity: 1;
  transition: opacity .15s;
}

@media (max-width: 768px) {
  .full img {
    display: none;
  }

  .full img.phone {
    display: block;
  }

  .btn-box a {
    display: block;
    margin-bottom: 5px;
  }

  .lang-box {
    margin-top: -5px;
  }
}


</style>
